import { Component, OnInit } from '@angular/core';
import { NzMessageService } from 'ng-zorro-antd';
import {
  AbstractControl,
  FormBuilder,
  FormGroup,
  Validators,
  FormsModule
} from '@angular/forms';
import { Base64 } from 'js-base64';
import { Router } from '@angular/router';
import { LoginService} from '../../services/login.service';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  validateForm: FormGroup;
  user_name = '';
  pass_word = '';
  constructor(private fb: FormBuilder, private message: NzMessageService, private router: Router, private loginService: LoginService) {
  }
  submitForm(): any {
    console.log(this.validateForm.value);
    if (this.validateForm.value.userName === '' || this.validateForm.value.userName === null) {
      this.message.warning('用户名不能为空');
      return false;
    }
    if (this.validateForm.value.password === '' || this.validateForm.value.password === null) {
      this.message.warning('密码不能为空');
      return false;
    }
    const base = Base64;
    const date = new Date().getTime();
    const res = date + ':' + this.validateForm.value.password;
    const encodePwd = base.encode(res);
    const Pwd = ['System', encodePwd];
    const pwd = Pwd.join(' ');
    const condition = {
      name: this.validateForm.value.userName,
      passwd: pwd
    };
    this.loginService.login(condition)
      .subscribe(data => {
        console.log(data);
        if ((<any>data).code === 0) {
          this.message.success('登录成功');
          setTimeout(() => {
            this.router.navigate(['/index']);
          }, 1000);
        } else {
          this.message.error((<any>data).msg);
        }
      });

  }

  ngOnInit() {
    this.validateForm = this.fb.group({
      userName: [null, [Validators.required]],
      password: [null, [Validators.required]],
      remember: [true]
    });
  }

}
